.table{display:flex;flex-direction:column;gap:calc(var(--row-margin)*.5)}.table div{display:flex;align-items:center;justify-content:space-between;padding-bottom:calc(var(--row-margin)*.5);border-bottom:1px solid var(--border-color)}.table div:last-child{padding-bottom:unset;border:unset}.table div p{text-wrap:nowrap}.table div p span{display:block;color:var(--foreground-dimmed-color);font-size:var(--caption-size)}.table div p:last-child{font-size:var(--caption-size)}