header{position:fixed;z-index:999;display:flex;align-items:center;justify-content:center;top:-1px;left:0;width:100%;height:var(--header);color:var(--foreground-color);background:var(--background-color);transition:translate .4s var(--ease);transform:translateY(-0.1%)}header nav{display:flex;align-items:center;justify-content:space-between;width:100%;max-width:var(--body);height:100%;padding:0 var(--margin);margin:0 auto}header nav span{font-size:var(--caption-size);font-weight:var(--font-weight);line-height:var(--font-height);letter-spacing:var(--font-spapcing);text-wrap:nowrap}header nav span:last-child a{background-repeat:no-repeat;background-image:linear-gradient(currentColor, currentColor),linear-gradient(currentColor, currentColor);background-size:51% 1px,50% 1px;background-position:0 95%,100% 95%;animation:linkSlideIn .4s var(--ease) forwards}header nav span:last-child a:hover{background-position:210% 95%,300% 95%;animation:linkSlideOut .4s var(--ease) forwards}