.row{z-index:0;display:grid;grid-template-columns:repeat(4, 1fr);grid-column:1/-1;gap:var(--grid-gap);width:100%;padding:var(--row-margin) 0;overflow:hidden}.row:last-child,.row.-fit,.row.-fix{background-image:unset}.row.-fit{display:block}.row.-fix{padding:calc(var(--row-margin)*.5) 0}@media screen and (max-width: 1023px){.row.-fix{grid-template-columns:repeat(2, 1fr)}.row.-fix :nth-child(2),.row.-fix :nth-child(3){display:none}}.row.-flow{display:block;-moz-column-count:4;column-count:4;-moz-column-fill:balance;column-fill:balance;gap:var(--grid-gap);grid-auto-flow:dense;widows:3;orphans:3}.row.-flow p{margin-bottom:var(--row-margin)}.row.-flow p:last-child{margin-bottom:unset}@media screen and (max-width: 1023px){.row.-flow{-moz-column-count:2;column-count:2}}@media screen and (max-width: 643px){.row.-flow{-moz-column-count:auto;column-count:auto}}@media screen and (max-width: 1023px){.row{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 643px){.row{grid-template-columns:auto}}.row .-spn100{grid-column:1/-1}.row .-spn50{grid-column:span 2}@media screen and (max-width: 643px){.row .-spn50{grid-column:auto}}section .row{opacity:0;background-repeat:no-repeat;background-image:linear-gradient(var(--border-color), var(--border-color)),linear-gradient(var(--border-color), var(--border-color));background-size:50% 1px,50% 1px;background-position:-300% 100%,-210% 100%}section .row.-on{animation:rowSlideUp 1s var(--ease) forwards}@keyframes rowSlideUp{from{background-position:-300% 100%,-210% 100%;transform:translateY(1rem);opacity:0}to{background-position:0 100%,100% 100%;transform:translateY(0);opacity:1}}a.row .title h3{display:inline;background-repeat:no-repeat;background-image:linear-gradient(currentColor, currentColor),linear-gradient(currentColor, currentColor);background-size:51% 2px,50% 2px;background-position:-300% 100%,-210% 100%;transition:all .4s var(--ease)}a.row .title h3::after{content:" ↗";display:inline-block;transition:all .2s var(--ease);opacity:0}a.row:hover .title h3{background-position:0 100%,100% 100%}a.row:hover .title h3::after{opacity:1}@media screen and (max-width: 767px){a.row .title h3::after{opacity:1}}