.tagline{display:flex;flex-direction:column;align-items:flex-start;grid-column:1/-1;gap:.25em;padding:var(--header) 0;overflow:hidden}.tagline div{display:flex;align-items:flex-start;background-repeat:no-repeat;background-image:linear-gradient(currentColor, currentColor),linear-gradient(currentColor, currentColor);background-size:50% 1px,50% 1px;background-position:-300% 100%,-210% 100%;overflow:hidden}.tagline div.-on{animation:taglineSlideIn 1s var(--ease) forwards}@keyframes taglineSlideIn{from{background-position:-300% 100%,-210% 100%}to{background-position:0 100%,100% 100%}}.tagline div span{font-size:var(--display-size);font-weight:var(--title-weight);line-height:1;letter-spacing:var(--title-spacing);opacity:0}.tagline div span.-on{animation:taglineSlideUp 1s var(--ease) forwards}@keyframes taglineSlideUp{from{transform:translateY(100%);opacity:0}to{transform:translateY(0);opacity:1}}