.fab{position:fixed;z-index:999;display:flex;align-items:center;justify-content:center;bottom:var(--margin);padding:1em;color:var(--background-color);background:var(--foreground-color);transition:all .4s var(--ease);font-size:var(--font-size);font-weight:var(--title-weight);line-height:1;letter-spacing:var(--font-spacing)}.fab.-center{left:50%}.fab.-left{left:var(--margin)}.fab.-right{right:var(--margin);font-weight:900}@media screen and (max-width: 643px){.fab{bottom:calc(var(--margin)*1.5);padding:.75em}}