html{display:flex;background:var(--background-color)}body{display:flex;width:100%;color:var(--foreground-color);background:var(--background-color);font-size:var(--font-size)}#root{width:100%}main,section{z-index:1;display:grid;grid-template-columns:repeat(8, 1fr);grid-column:1/-1;-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap)}@media screen and (max-width: 1023px){main,section{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 643px){main,section{grid-template-columns:repeat(2, 1fr)}}main{max-width:var(--body);padding:0 var(--margin);margin:0 auto}section{background:var(--background-color)}section::before{content:"";position:absolute;top:0;left:50%;width:100vw;height:100%;background:var(--background-color);transform:translateX(-50%)}section:first-child{padding-top:calc(var(--header)*2.5) !important}section:last-child{padding-bottom:calc(var(--header)*2.5) !important}section.hero{position:relative;top:0;left:0;width:100%}section.hero .tagline{padding-bottom:calc(var(--header)*2.5)}section.cover{position:sticky;z-index:-1;display:flex;align-items:center;top:0;right:0;bottom:0;left:0;width:100%;height:100vh}section.cover video{position:absolute;z-index:0;left:50%;width:100vw;height:100%;transform:translateX(-50%);-o-object-fit:cover;object-fit:cover;-o-object-position:50% 50%;object-position:50% 50%;pointer-events:none;filter:brightness(80%)}section.content{padding:var(--row-margin) 0}